@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700&display=swap");
/* navbar */
.navbar {
  position: fixed;
  top: 0;
  width: 100%;
  height: 77px;
  left: 0;
  background-color: var(--white-color);
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 30px;
  z-index: 1000;
  box-shadow: 0 0 2px var(--grey-color-light);
}
.logo_item{
  display: flex;
  align-items: center;
  column-gap: 20px;
  margin-left: -15px;
}

.logo_item img{
  height: 30px; /* Adjust logo height */
  object-fit: contain; /* Ensure the logo maintains aspect ratio */
}

.logo_item i{
  color:#103782;
  position: relative;
  font-size: 25px;
  min-width: 50px;
  line-height: 50px;
  border-radius: 50%;
  display: inline-block;
  text-align: center;
}

.logo_item i:hover{
  cursor: pointer;
  background-color:#eaedf2;
}

