.settings {
    display: flex;
    flex-direction: column;
}
    
.settings-title-content {
    padding-top: 15px;
    display: flex;
    font-size: 12px;
    font-weight: bold;
    color: #103782;
    justify-content: space-between;
    padding-left: 10px;
    padding-right: 10px;
}

.settings-title {
    padding-top: 10px;
}

hr {
    margin-left: 5px;
    margin-right: 5px;
    border-color: #103782;
}

.settings-widgets {
    display: flex;
    flex-direction: column;
    padding: 2px 5px;
    height: calc(100vh - 250px);
}

.settings-widgets .widget-upper{
    display: flex;
    max-width: 100%;
    min-height: calc(40vh - 80px);
}

.settings-widgets .widget-upper .widget{
    margin: 1px;
    display: flex;
    flex-direction: column;
    width: 50%;
}

.settings-widgets .widget-upper .widget span{
    margin-left: 1px;
    margin-right: 1px;
    padding-left: 3px;
    font-size: 15px;
    color: white;
    background-color: #103782;
}

.settings-widgets .widget-upper .widget .connection-view {
    margin: 1px;
    border: 1px solid #bbb;
    padding: 10px;
    height: calc(50vh - 170px);
}

.settings-widgets .widget-upper .widget .connection-view .upper{
    display:flex;
    gap: 110px;
    justify-content: center;
    align-items: center;
    height: 50%;
}

.settings-widgets .widget-upper .widget .connection-view .upper span{
    margin-left: 24px;
    color: black;
    background-color: white;
}

.settings-widgets .widget-upper .widget .connection-view .upper .dropdown{
    width: 290px;
    height: 40px;
    font-size: 12px;
    cursor: pointer;
    border: 1px solid #aaa;
    border-radius: 5px;
}

.settings-widgets .widget-upper .widget .connection-view .lower{
    display:flex;
    gap: 10px;
